
@font-face {
  font-family: 'icomoon';
  src: url("/static/fonts/icomoon.eot?wxcqzo");
  src: url("/static/fonts/icomoon.eot?wxcqzo#iefix") format('embedded-opentype'), url("/static/fonts/icomoon.ttf?wxcqzo") format('truetype'), url("/static/fonts/icomoon.woff?wxcqzo") format('woff'), url("/static/fonts/icomoon.svg?wxcqzo#icomoon") format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
/* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
/* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.header {
  color: #fff;
  position: relative;
  overflow: hidden;
  background: rgba(7,17,27,0.5);
/*background #AB9795*/
}
.header .content-wrapper {
  position: relative;
  padding: 24px 12px 18px 24px;
  font-size: 0;
}
.header .content-wrapper .avatar {
  vertical-align: top;
  display: inline-block;
}
.header .content-wrapper .avatar img {
  border-radius: 4px;
}
.header .content-wrapper .content {
  vertical-align: top;
  font-size: 12px;
  margin-left: 16px;
  display: inline-block;
}
.header .content-wrapper .content .title {
  margin: 2px 0 8px 0;
}
.header .content-wrapper .content .title .brand {
  width: 30px;
  height: 18px;
  line-height: 18px;
  text-align: center;
  display: inline-block;
  background: #ff4500;
}
.header .content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 18px;
  font-weight: bold;
}
.header .content-wrapper .content .description {
  margin-bottom: 10px;
  line-height: 12px;
  font-size: 12px;
}
.header .content-wrapper .content .support .icon {
  vertical-align: top;
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background: #ff8c00;
}
.header .content-wrapper .content .support .text {
  font-size: 10px;
  line-height: 12px;
}
.header .content-wrapper .support-count {
  position: absolute;
  bottom: 14px;
  right: 12px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  border-radius: 14px;
  background: rgba(0,0,0,0.2);
  text-align: center;
}
.header .content-wrapper .support-count .count {
  font-size: 12px;
  vertical-align: top;
}
.header .content-wrapper .support-count .icon-circle-right:before {
  line-height: 24px;
  margin-left: 2px;
  font-size: 10px;
  content: "\ea42";
}
.header .bulletin-wrapper {
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 22px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(101,79,89,0.5);
}
.header .bulletin-wrapper .bulletin-title {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
  width: 25px;
  height: 12px;
  line-height: 12px;
  background: #fff;
  color: #000;
  border-radius: 2px;
  margin-top: 8px;
}
.header .bulletin-wrapper .bulletin-text {
  vertical-align: top;
  font-size: 12px;
  margin: 0 4px;
}
.header .bulletin-wrapper .icon-circle-right:before {
  position: absolute;
  right: 6px;
  bottom: 2px;
  line-height: 24px;
  margin-left: 2px;
  font-size: 10px;
  content: "\ea42";
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(1,17,27,0.8);
  -webkit-backdrop-filter: blur(10px);
}
.header .detail .detail-wrapper {
  min-height: 100%;
  overflow: hidden;
}
.header .detail .detail-wrapper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.header .detail .detail-wrapper .detail-main .name {
  line-height: 16px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.header .detail .detail-wrapper .detail-main .star {
  margin-top: 20px;
}
.header .detail .detail-wrapper .detail-main .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 20px auto 22px auto;
}
.header .detail .detail-wrapper .detail-main .title .line {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
.header .detail .detail-wrapper .detail-main .title .text {
  padding: 0 12px;
  font-size: 14px;
}
.header .detail .detail-wrapper .detail-main .supports {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .supports .support-item {
  padding: 0 12px;
  margin-bottom: 12px;
  font-size: 12px;
}
.header .detail .detail-wrapper .detail-main .supports .support-item:last-child {
  margin-bottom: 0;
}
.header .detail .detail-wrapper .detail-main .supports .support-item:last-child .icon {
  background: #f00;
}
.header .detail .detail-wrapper .detail-main .supports .support-item:nth-child(1) .icon {
  background: #1b9bd8;
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon {
  text-align: center;
  display: inline-block;
  line-height: 14px;
  width: 14px;
  height: 14px;
  color: #fff;
  margin-right: 6px;
  background: #008000;
}
.header .detail .detail-wrapper .detail-main .bulletin {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .bulletin p.content {
  padding: 0 12px;
  font-size: 12px;
  line-height: 24px;
  text-align: justify;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  line-height: 32px;
  margin: -50px auto 0 auto;
  clear: both;
  font-size: 20px;
  text-align: center;
  border-radius: 16px;
  border: 1px solid #fff;
}


