
.star {
  font-size: 0;
  text-align: center;
}
.star .icon-star-full {
  display: inline-block;
  background-repeat: no-repeat;
}
.star .icon-star-full.on:before {
  color: #ffd700;
}
.star .icon-star-full.half:before {
  color: #9e8970;
}
.star .icon-star-full.off:before {
  color: #888;
}
.star .icon-star-full:before {
  content: "\e9d9";
  line-height: 40px;
  margin-left: 2px;
  font-size: 22px;
  padding: 0 5px;
  color: #f00;
}


