
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', Arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.seller {
  position: absolute;
  top: 174px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.seller .overview {
  padding: 18px;
}
.seller .overview .title {
  font-size: 14px;
  margin-bottom: 8px;
  line-height: 14px;
  color: #07111b;
}
.seller .overview .desc {
  line-height: 18px;
  font-size: 0;
  position: relative;
  padding: 14px 0;
}
.seller .overview .desc:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.seller .overview .desc .star {
  display: inline-block;
  vertical-align: top;
  margin-right: 8px;
  margin-left: -6px;
}
.seller .overview .desc .star .icon-star-full:before {
  font-size: 18px;
  padding: 0 2px;
  line-height: 15px;
}
.seller .overview .desc .text {
  margin-right: 12px;
  vertical-align: top;
  display: inline-block;
  font-size: 10px;
  color: #4d555d;
}
.seller .overview .remark {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
}
.seller .overview .remark .block {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  border-right: 1px solid rgba(7,17,27,0.1);
}
.seller .overview .remark .block:last-child {
  border: none;
}
.seller .overview .remark .block h2 {
  font-size: 10px;
  color: #93999f;
  margin-bottom: 4px;
  line-height: 10px;
}
.seller .overview .remark .block .content {
  line-height: 24px;
  font-size: 10px;
  color: #07111b;
}
.seller .overview .remark .block .content .stress {
  font-size: 24px;
}
.seller .bulletin {
  padding: 18px 18px 0 18px;
}
.seller .bulletin .title {
  font-size: 14px;
  margin-bottom: 8px;
  line-height: 14px;
  color: #07111b;
}
.seller .bulletin .content-wrapper {
  padding: 0 12px 16px 12px;
  position: relative;
}
.seller .bulletin .content-wrapper:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.seller .bulletin .content-wrapper .content {
  line-height: 24px;
  font-size: 12px;
  color: #07111b;
}
.seller .supports {
  margin: 18px 10px;
  padding-bottom: 18px;
}
.seller .supports .support-item {
  padding: 6px 12px;
  margin-bottom: 12px;
  font-size: 12px;
}
.seller .supports .support-item:last-child {
  margin-bottom: 0;
}
.seller .supports .support-item:last-child .icon {
  background: #f00;
}
.seller .supports .support-item:nth-child(1) .icon {
  background: #1b9bd8;
}
.seller .supports .support-item .icon {
  text-align: center;
  display: inline-block;
  line-height: 14px;
  width: 14px;
  height: 14px;
  color: #fff;
  margin-right: 6px;
  background: #008000;
}


