
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', Arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.shop-cart {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 48px;
}
.shop-cart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
  font-size: 0;
}
.shop-cart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.shop-cart .content .content-left .logo-wrapper {
  position: relative;
  display: inline-block;
  vertical-align: top;
  position: relative;
  top: -10px;
  margin: 0 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #141d27;
}
.shop-cart .content .content-left .logo-wrapper .logo {
  width: 100%;
  height: 100%;
  background: #2b343c;
  border-radius: 50%;
  text-align: center;
}
.shop-cart .content .content-left .logo-wrapper .logo .icon-yahoo:before {
  content: "\eabb";
  color: #80858a;
  font-size: 24px;
  line-height: 44px;
}
.shop-cart .content .content-left .logo-wrapper .logo.highlight {
  background: #0aa0dc;
}
.shop-cart .content .content-left .logo-wrapper .logo.highlight .icon-yahoo:before {
  color: #fff;
}
.shop-cart .content .content-left .logo-wrapper .num {
  position: absolute;
  color: #fff;
  top: 0;
  right: 0;
  font-size: 9px;
  width: 24px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border-radius: 16px;
  font-weight: 700;
  background: #f01414;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
}
.shop-cart .content .content-left .price {
  display: inline-block;
  vertical-align: top;
  line-height: 24px;
  margin-top: 12px;
  padding-right: 12px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255,255,255,0.1);
  font-size: 16px;
  font-weight: 700px;
  color: #80858a;
}
.shop-cart .content .content-left .price.highlight {
  color: #fff;
}
.shop-cart .content .content-left .desc {
  display: inline-block;
  vertical-align: top;
  line-height: 24px;
  margin-top: 13px;
  padding-left: 12px;
  box-sizing: border-box;
  font-size: 10px;
  font-weight: 700px;
  color: #80858a;
}
.shop-cart .content .content-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 102;
          flex: 0 0 102;
  width: 105px;
}
.shop-cart .content .content-right .pay {
  height: 48px;
  line-height: 48px;
  font-size: 12px;
  font-weight: 700;
  color: #80858a;
  text-align: center;
  background: #2b333b;
}
.shop-cart .content .content-right .pay.enough {
  background: #00b43c;
  color: #fff;
}
.shop-cart .content .content-right .pay.not-enough {
  background: #2b333b;
}
.shop-cart .shopcart-list {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.shop-cart .shopcart-list.fold-enter,
.shop-cart .shopcart-list.fold-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
.shop-cart .shopcart-list.fold-enter-active,
.shop-cart .shopcart-list.fold-leave-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.shop-cart .shopcart-list .list-header {
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  background: #f3f5f7;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.shop-cart .shopcart-list .list-header .title {
  float: left;
  font-size: 14px;
  color: #07111b;
}
.shop-cart .shopcart-list .list-header .empty {
  float: right;
  font-size: 12px;
  color: #00a0dc;
}
.shop-cart .shopcart-list .list-content {
  padding: 0 18px;
  max-height: 217px;
  background: #fff;
  overflow: hidden;
}
.shop-cart .shopcart-list .list-content .food {
  position: relative;
  padding: 12px 0;
  box-sizing: border-box;
  position: relative;
}
.shop-cart .shopcart-list .list-content .food:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.shop-cart .shopcart-list .list-content .food .name {
  line-height: 24px;
  font-size: 14px;
  color: #07111b;
}
.shop-cart .shopcart-list .list-content .food .price {
  position: absolute;
  right: 99px;
  bottom: 12px;
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
  line-height: 24px;
}
.shop-cart .shopcart-list .list-content .food .cartcontrol-wrapper {
  min-width: 90px;
  position: absolute;
  right: 0;
  bottom: 6px;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 31;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.6);
}
.list-mask.fade-transition {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  opacity: 1;
}
.list-mask.ade-enter-active,
.list-mask.ade-leave-active {
  opacity: 0;
  background: rgba(7,17,27,0);
}


