
.move-enter-active,
.move-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0);
          transform: translate3D(24px, 0, 0);
}
.move-enter {
  opacity: 0;
  -webkit-transform: translate3D(40px, 0, 0);
          transform: translate3D(40px, 0, 0);
}
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
}
.cartcontrol .cart-decrease.icon-minus:before {
  padding: 3px;
  content: "\ea0b";
  font-size: 10px;
  width: 10px;
  height: 10px;
  color: #fff;
  background: #00a0dc;
  border-radius: 50%;
}
.cartcontrol .cart-count {
  display: inline-block;
  padding: 3px 6px;
  font-size: 15px;
  height: 12px;
  min-width: 18px;
  text-align: center;
  line-height: 12px;
  color: #000;
  font-weight: 400;
}
.cartcontrol .cart-add {
  display: inline-block;
  padding: 6px;
}
.cartcontrol .cart-add.icon-plus:before {
  padding: 3px;
  content: "\ea0a";
  font-size: 10px;
  width: 10px;
  height: 10px;
  color: #fff;
  background: #00a0dc;
  border-radius: 50%;
}


